<template>
    <div id="container">
        <div class="table">
            <div class="head">
                <div class="head-item">Table Number</div>
                <div class="head-item">Order Items</div>
                <div class="head-item">Note</div>
                <div class="head-item">Served</div>
            </div>
            <div class="content" v-for="order in orders" :key="order.isServed">
                <div class="content-item" style="padding-left:7vw">{{ order.tableNumber }}</div>
                <div class="content-item" id="items">
                    <span v-for="j in order.order" :key="j.name">
                        {{ j.name }} x {{ j.quan }}
                    </span>
                </div>
                <div class="content-item" style="padding-right:8vw;display:flex;flex-wrap: wrap;width: 19vw;">{{ order.note }}</div>
                <div class="content-item"><button class="btn-green" @click="order_served(order._id)">Served</button>
                </div>
            </div>
        </div>
        <!-- <table>
            <thead>
                <th>Table Number</th>
                <th>Items</th>
                <th>Note</th>
                <th>Served Button</th>
            </thead>
            <tbody>
                <tr v-for="order in orders" :key="order.isServed">
                    <td style=" text-align: center;">{{ order.tableNumber }}</td>
                    <td class="items">
                        <span v-for="j in order.order" :key="j.name" >â‡’  {{ j.name }} x {{ j.quan }}</span>
                    </td>
                    <td>
                        {{ order.note }}
                    </td>
                    <td class="td-btn "><button class="btn-green" @click="order_served(order._id)">Served</button></td>
                </tr>
            </tbody>
        </table> -->
    </div>
</template>
<script>
export default {
    name: 'mainWindow',
    data() {
        return {
            orders: []
        }
    },
    created() {
        this.fetchOrders();
    },
    methods: {
        fetchOrders() {
            setInterval(() => {
                fetch('http://localhost/orders/fetchNotServedOrder')
                    .then((response) => {
                        if (response.ok) {
                            return response.json();
                        }
                        else {
                            console.log("error");
                        }
                    }).then((data) => {
                        this.orders = data.reverse();
                    })
            }, 1000)
        },
        order_served(id) {
            fetch('http://localhost/orders/updateServedStatus' + id, {
                method: 'PATCH',
                headers: {
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify({
                    isServed: true
                })
            });
        }
    }
}
</script>
<style>
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap');

.content {
    display: flex;
    justify-content: space-around;
    margin: 1vw;
    padding-bottom: 1vw;
    border-bottom: 1px solid beige;
}

.content-item {
    color: rgb(255, 194, 170);
    letter-spacing: 0.1vw;
    font-size: 1.4vw;
}

#items {
    display: flex;
    flex-direction: column;
    padding-left: 11.4vw;
}

* {
    font-family: 'Poppins', sans-serif;
}

body {

    overflow-x: hidden;
    /* background-size: 400% 400%; */
    background-image: url('../assets/bg.jpg');
    background-repeat: repeat;
    background-size: cover;
    object-fit: contain;
    animation: pan 15s infinite alternate linear;
    resize: both;
}

@keyframes pan {
    100% {
        background-position: 100% 20%;
    }
}

::-webkit-scrollbar {
    width: 20px;
}

::-webkit-scrollbar-track {
    background: #ffffff;
}

::-webkit-scrollbar-thumb {
    background: #d39f75;
}

#container {
    background-color: rgba(3, 3, 2, 0.822);
    border: 2px solid rgba(0, 0, 0, 0.897);
    border-radius: 10px;
    margin: 0.5vw;
    padding: 0.4vw;
}

.head {
    border-bottom: 2px solid beige;
    width: 100%;
    color: #d39f75;
    letter-spacing: 0.1vw;
    display: flex;
    justify-content: space-around;
    padding-bottom: 1vw;

}

.head-item {
    font-size: 1.4vw;
    font-weight: bold;
    letter-spacing: 0.1vw;
}

/* Button green */
.btn-green {
    border:2px solid #edf9fa;
    font-size: 1vw;
    color: #aaffb1;
    cursor: pointer;
    position: relative;
    overflow: hidden;
    transition: all 0.3s;
    border-radius: 12px;
    box-shadow: 0 1px 0 1px rgb(255, 255, 255);
    padding: 0.3vw 1vw 0.3vw 1vw;
    margin: 0vw 0.2vw 0vw -8vw;
    width: 7vw;
    letter-spacing: 0.1vw;
}

.btn-green:before {
    content: "";
    position: absolute;
    width: 100px;
    height: 120%;
    background-color: #0c4107;
    top: 50%;
    transform: skewX(30deg) translate(-150%, -50%);
    transition: all 0.5s;
}

.btn-green:hover {
    background-color: #1c5833;
    color: rgb(255, 255, 255);
    box-shadow: 0 1px 0 1px rgb(0, 0, 0);
    transform: translateY(-2px);
}

.btn-green:hover::before {
    transform: skewX(30deg) translate(150%, -50%);
}

.btn-green:active {
    transform: scale(0.9);
}

/* checkBox */


</style>